import {EventSocket, ServiceSocket} from '../../core/service/service-socket';
import {DispatchEventT62, EventT62} from '../../core/DispatchEventT62';
import {EnumEngine} from '../../core/EnumEngine';


export class UserService {

  private socketService: ServiceSocket;

  public constructor() {
    DispatchEventT62.registrationObj(EnumEngine.UserService, this);
    DispatchEventT62.addEventListener(EnumEngine.ServiceSocket, EventSocket.connect, this.connectSocket, this);
  }

  private connectSocket(e: EventT62): void {
    this.socketService = e.dispatcher;
    this.socketService.setCallbackNewMess(this.newMess.bind(this));
    this.socketService.send({command: '/terminal/test'});
  }

  public setCallBack(f:Function){
    this.socketService.setCallbackNewMess(f);
  }
  
  public sendMess(mess: Object) {
    this.socketService.send(JSON.stringify(mess));
  }

  public newMess(mess: string) {
    let resp = {data: mess};
    try {
      resp = JSON.parse(mess);
    } catch (e) {
    }
    if ((<any>resp).returnID) {
      DispatchEventT62.dispatchEvent(this, (<any>resp).returnID.toString(), resp);
    }
  }


}
