import {Component} from '@angular/core';
import {ServiceController} from './core/service/ServiceController';

@Component({
  selector: 't62-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'client';

  public host = "";

  constructor() {
    if(window.location.host === 'localhost:4200'){
      this.host = "localhost:8081"
    }else{
      this.host = window.location.host;
    }

    ServiceController.getInst().initService(null, false, true, true);
    ServiceController.getInst().serviceSocket.connect('ws://localhost:8081');

  }

}

