import {Utils} from '../utils/utils';
import {EnumEngine} from '../EnumEngine';
import {DispatchEventT62, EventT62} from '../DispatchEventT62';


declare const io: any;


export class ServiceSocket {

  private callbackNewMess: Function = null;
  private socket: any;


  constructor() {
    DispatchEventT62.registrationObj(EnumEngine.ServiceSocket, this);
  }

  public connect(host: string, callbackNewMess: Function = null) {
    this.callbackNewMess = callbackNewMess;
    this.socket = io.connect(host + '?id=1', {
      path: '/events',
      reconnection: false
    });
    this.socket.on('connect', this._connect.bind(this));
    this.socket.on('disconnect', this.disconnect.bind(this));
    this.socket.on('error', this.error.bind(this));
    this.socket.on('news', this.news.bind(this));
    this.socket.on('init', this.init.bind(this));

    DispatchEventT62.addEventListener(EnumEngine.ServiceSocket, EventSocket.sendSocketMess, this.send, this);
  }

  private _connect(data): void {
    DispatchEventT62.dispatchEvent(this, EventSocket.connect);
  }

  private init(data): void {

  }

  public setCallbackNewMess(f: Function) {
    if (f) {
      this.callbackNewMess = f;
    }
  }

  private disconnect(data): void {
    console.error('disconnect!!! = > ' + data);
    DispatchEventT62.dispatchEvent(this, EventSocket.disconnect);
  }

  private error(error): void {
    console.error('ERROR!!! = > ' + error);
    DispatchEventT62.dispatchEvent(this, EventSocket.error, error);
  }

  private news(mess): void {
    console.log(mess);
    if (mess.command === 'result') {
      mess = mess.result;
    }
    if (Utils.checkFieldNull(this.callbackNewMess)) this.callbackNewMess(mess);
    DispatchEventT62.dispatchEvent(this, EventSocket.nMessage, mess);
  }

  public send(mess): void {
    if (Utils.checkFieldNull(mess) && this.socket.connected) {
      if (mess.constructor.name === 'EventT62') {
        mess = (<EventT62>mess).dataDisp;
      }
      if (mess.constructor.name !== 'String') {
        mess = JSON.stringify(mess);
      }
      this.socket.emit('mess', mess);
      console.log('send = ' + mess);
    } else {
      console.log('socket closed!');
    }


  }

  public isConnect(): boolean {
    return this.socket.connected;
  }


}

export enum EventSocket {
  connect = 'connect', disconnect = 'disconnect', error = 'error', nMessage = 'nMessage', sendSocketMess = 'sendSocketMess'
}
