import {Component, ViewChild} from '@angular/core';
import {TerminalService} from 'primeng/components/terminal/terminalservice';
import {DispatchEventT62, EventT62} from '../../core/DispatchEventT62';
import {EnumEngine} from '../../core/EnumEngine';
import {UserService} from '../service/UserService';
import {BaseComponent} from '../../core/BaseComponent';

declare const jQuery;

@Component({
  selector: 't62-terminal',
  templateUrl: './terminal.html',
  styles: [],
  providers: [TerminalService]
})
export class TerminalComponent extends BaseComponent {
  display: boolean = true;
  @ViewChild('terminal') terminal: any;
  private _id;


  constructor() {
    super();
    this._id = Math.floor(Math.random() * 99999999999);
    DispatchEventT62.registrationObj('terminal' + this._id, this);


  };

  public ngAfterViewInit(): void {
    this.initTerminal();
  }


  private respTerminal(data: EventT62) {

    if (data.dataListener) {
      data.dataListener.set_mask(false);
      const msg = data.dataDisp.msg;
      const arr = msg.split('\\n');
      data.dataListener.resume();
      arr.map(v => {
        if (v.length > 1) data.dataListener.echo(v);
      });
    }
  }


  private initTerminal() {
    jQuery(($, undefined) => {
      $(this.terminal.nativeElement).terminal((command, term) => {
        if (command !== '') {
          DispatchEventT62.addEventListener(EnumEngine.UserService, this._id.toString(), this.respTerminal, this, term);
          term.pause();

          const initTerminal = {
            returnID: this._id,
            command: '/terminal/initTerminal',
            cmdCommand: command
          };
          const us: UserService = DispatchEventT62.getComponent(EnumEngine.UserService);
          us.sendMess(initTerminal);
        }
      }, {
        greetings: 'Hello',
        name: 'js_demo',
        height: 400,
        width: 800,
        prompt: 'user> '
      });
    });
  }

  parseMess(st: string) {
    const arr = this.toUTF8Array(st);
    return this.toStringArr(arr);
  }


  toUTF8Array(str) {
    var utf8 = [];
    for (var i = 0; i < str.length; i++) {
      var charcode = str.charCodeAt(i);
      if (charcode < 0x80) utf8.push(charcode);
      else if (charcode < 0x800) {
        utf8.push(0xc0 | (charcode >> 6),
          0x80 | (charcode & 0x3f));
      }
      else if (charcode < 0xd800 || charcode >= 0xe000) {
        utf8.push(0xe0 | (charcode >> 12),
          0x80 | ((charcode >> 6) & 0x3f),
          0x80 | (charcode & 0x3f));
      }
      // surrogate pair
      else {
        i++;
        charcode = ((charcode & 0x3ff) << 10) | (str.charCodeAt(i) & 0x3ff);
        utf8.push(0xf0 | (charcode >> 18),
          0x80 | ((charcode >> 12) & 0x3f),
          0x80 | ((charcode >> 6) & 0x3f),
          0x80 | (charcode & 0x3f));
      }
    }
    return utf8;
  }

  toStringArr(arr: Array<number>) {
    let resp = '';
    for (let r = 0; r < arr.length; r++) {
      resp += String.fromCharCode(arr[r]);
    }
    return resp;
  }


}


