  public static checkFieldObj(obj: any, list: any[] = Utils.defCheckList): boolean {
    try {
      let boo = true;
      if (obj instanceof Array) {
        list.forEach(v => {
          if ((<Array<any>>obj).indexOf(v) > -1) boo = false;
        })
      } else {
        for (const f in obj) {
          if (list.indexOf(obj[f]) > -1) boo = false;
        }
      }
      return boo;
    } catch (e) {
      throw new Error("'obj' must be Array or Map ({key:value})");
    }


  }


  public static addToSelectOption(select: any, listOpt: any, targetSelect: any = null) {
    const parent = (select.nativeElement !== undefined) ? select.nativeElement : select;
    for (let a = 0; a < listOpt.length; a++) {
      const o = listOpt[a];
      const opt = document.createElement('option');
      opt.value = o;
      opt.innerHTML = o;
      parent.appendChild(opt);
      if (targetSelect !== null) {
        if (targetSelect === o) {
          parent.selectedIndex = a;
        }
      }
    }
  }

  public static addSelectToList(listOpt: any, targetSelect: any = null) {
    const list = [];
    for (let a = 0; a < listOpt.length; a++) {

      list.push([listOpt[a], null]);
      if (targetSelect !== null) {
        if (targetSelect === listOpt[a]) {
          list[a][1] = true;
        }
      }
    }
    return list;
  }

  public static selectThemeList(list: any, theme: any = null, ind: any = null): Array<any> {
    const ll = [];
    for (let a = 0; a < list.length; a++) {
      ll.push([list[a][0], null]);
      // list[a][1] = null;
      if (theme !== null) {
        if (ll[a][0] === theme) {
          ll[a][1] = true;
        }
      }
    }
    if (theme === null) {
      if (ind !== null) {
        if (ind >= 0 && ind < ll.length) {
          ll[ind][1] = true;
        }
      }
    }
    return ll;
  }


  public static setTextInElement(elem: any, text: any) {
    if (elem.nativeElement !== undefined) {
      elem = elem.nativeElement;
    }
    if (elem.localName === 'div') {
      elem.innerHTML = text;
    }
  }

  public static randomString(lenght: number): string {
    let text = '';
    const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

    for (let i = 0; i < lenght; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));

    return text;
  }

  public static getRandomColor(): string {
    const letters = '0123456789ABCDEF';
    let color = '#';
    for (let i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }


  /**
   * for game RID
   */
  public static getRID() {
    return Math.floor(Math.random() * 100000000000000);
  }

  public static mouseEventDocument(target: any): void {
    const rect: any = target.getBoundingClientRect();
    let mouseEvent;
    /*  mouseEvent = new MouseEvent('mousedown', {clientX: rect.x+rect.width/2, clientY: rect.y+rect.height/2});
        target.dispatchEvent(mouseEvent);
        mouseEvent = new MouseEvent('mouseup', {clientX: rect.x+rect.width/2, clientY: rect.y+rect.height/2});
        target.dispatchEvent(mouseEvent);
    */
    if (!rect.x || !rect.y) return;
    mouseEvent = new MouseEvent('click', {
      clientX: rect.x + rect.width / 2, clientY: rect.y + rect.height / 2, bubbles: true,
      cancelable: true
    });
    target.dispatchEvent(mouseEvent);
  }

  public static setAllParamsToUpper(obj: any): any {
    Object.keys(obj).map(v => {
      obj[v.toUpperCase()] = obj[v];
      delete obj[v];
    })
    return obj;
  }

