  public static getAttributeForField(obj: any, field: string, attrib: string): string {
    if (obj && obj[field]) {
      if (obj[field] instanceof Array && obj[field].length === 2) {
        if (obj[field][1]['$'][attrib]) return obj[field][1]['$'][attrib];
      }
    }
    return null;
  }

  public static getAttrib(attribute: any, field: string): any {
    const edit: string = Utils.getAttributeForField(attribute, field, 'EDIT');
    const rq: string = Utils.getAttributeForField(attribute, field, 'RQ');
    const vis: string = Utils.getAttributeForField(attribute, field, 'VISIBLE');

    const res = {
      isDisable: (edit) ? (edit === 'T') ? false : true : false,
      isRequired: (rq) ? (rq === 'T') ? true : false : false,
      visible: (vis) ? (vis === 'T') ? true : false : true,
    }
    return res;
  }


