import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Utils} from '../utils/utils';
import {ServiceEndPoint} from './service-end-point';
import {IService} from './IService';
import {DispatchEventT62} from '../DispatchEventT62';
import {EnumEngine} from '../EnumEngine';


export class ServiceHTTP implements IService {

  private headersJSON: HttpHeaders = new HttpHeaders({

    'Access-Control-Allow-Origin': '*',
    // 'Access-Control-Allow-Headers': 'Content-Type',
    //  'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
    'Content-Type': 'application/json',
    'type': 'POST',
    'dataType': 'json',
    'mode': 'test'
  });
  private headersXML: HttpHeaders = new HttpHeaders({

    'Access-Control-Allow-Origin': '*',
    // 'Access-Control-Allow-Headers': 'Content-Type',
    // 'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
    'Content-Type': 'application/text',
    'type': 'POST',
    'dataType': 'xml'
  });


  constructor(private  http: HttpClient) {
    DispatchEventT62.registrationObj(EnumEngine.ServiceHTTP, this);
  }

  /*get(link: string): Observable<any> {
    if (this.app_config.isShowErrorPage) return;
    if (!Utils.checkFieldNull(link, Utils.defCheckList)) {
      return null;
    }
    DispatchEventT62.getComponent('spinner').showSpinner();
    return this.http.get(this.serviceConf.link_test_server + link)
     .map(response => {
      DispatchEventT62.getComponent('spinner').hideSpinner();
      return response;
    }, error2 => {
      DispatchEventT62.getComponent('spinner').hideSpinner();
      console.log(error);
      this.app_config.showErrorPage(true);
    });


  }*/
  public get(link: string, callBack: Function): void {

    if (!Utils.checkFieldNull(link, Utils.defCheckList)) {
      return;
    }
    DispatchEventT62.getComponent('spinner').showSpinner();
    this.http.get(link)
      .subscribe(response => {
        DispatchEventT62.getComponent('spinner').hideSpinner();
        if (callBack !== null) {
          callBack(response);
        }
      }, error2 => {
        DispatchEventT62.getComponent('spinner').hideSpinner();
        console.log(error2.error);

      });
  }

  public post(data: any = null, link: string|ServiceEndPoint, callBack: Function, contentType: string = 'json'): void {
    console.info('link request ->' + link);
    console.info('mess request ->' + data);
    if (!Utils.checkFieldNull(link, Utils.defCheckList)) {
      return;
    }
    if (typeof data !== 'string') {
      data = (data !== null) ? JSON.stringify(data) : null;
    }

    if (!Utils.checkFieldNull(data, Utils.defCheckList)) {
      return;
    }

    const headers = (contentType === 'json') ? this.headersJSON : this.headersXML;
    const responseType: any = (contentType === 'json') ? 'json' : 'text';
    // (<any>headers).headers.set('mode', (<AppComponent>DispatchEventT62.getComponent(EnumEngine.AppComponent)).getAPP_CONFIG().mode);

    DispatchEventT62.getComponent('spinner').showSpinner();
    this.http.post(link, data, {headers: headers, responseType: responseType})
      .subscribe(response => {
        console.log(response);
        DispatchEventT62.getComponent('spinner').hideSpinner();
        if (callBack !== null) {
          callBack(response);
        }
      }, error2 => {
        console.error(error2.error);
        DispatchEventT62.getComponent('spinner').hideSpinner();
      });
  }

  public authorize(data: any = null, link: string, callBack: Function): void {
    console.info('link request ->' + link);
    console.info('mess request ->' + data);
    if (!Utils.checkFieldNull(link, Utils.defCheckList)) {
      return;
    }
    if (typeof data !== 'string') {
      data = (data !== null) ? JSON.stringify(data) : null;
    }

    if (!Utils.checkFieldNull(data, Utils.defCheckList)) {
      return;
    }

    const header = {

      'Access-Control-Allow-Origin': '*',
      // 'Access-Control-Allow-Headers': 'Content-Type',
      //  'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
      'Content-Type': 'application/x-www-form-urlencoded',
      'type': 'POST'
    };


    DispatchEventT62.getComponent('spinner').showSpinner();
    this.http.post(link, data, {headers: header})
      .subscribe(response => {
        console.log(response);
        DispatchEventT62.getComponent('spinner').hideSpinner();
        if (callBack !== null) {
          callBack(response);
        }
      }, error2 => {
        console.error(error2.error);
        DispatchEventT62.getComponent('spinner').hideSpinner();
      });
  }

}


