import {Utils} from "./utils/utils";

export class DispatchEventT62 {

  /**
   * Диспатчер подій тільки для зареєстрованих об"єктів.
   * реєстрація необхідна для того, щоб ми мали змогу добавляти слухача не залежно від того
   * чи маємо ми ссилку на цей об"єкт, враховуючи на архітектуру angular і не потрібно наслідувати і імплетувати
   * різні класси.
   *
   * бажано реєстрацію проводити в конструкторі класса
   *
   *
   *
   *
   * @type {{}}
   * @private
   */
  private static _isRun: boolean = false;
  private static _temporarilyList: Array<Array<any>> = [];
  private static _temporarilyListRemove: Array<Array<any>> = [];
  private static _temporarilyListDisp: Array<Array<any>> = [];
  private static _objRegisterClass: Map<string, any> = new Map();
  private static _objEventListener: Map<string, Map<string, Array<EventT62>>> = new Map<string, Map<string, Array<EventT62>>>();


  public static registrationObj(name: string, obj: any): void {
    if (Utils.checkFieldNull(name)) {
      if (Utils.checkFieldNull(obj, Utils.defCheckList2)) {
        this._objRegisterClass.set(name, obj);
      }
    }
  }

  public static getComponent(nameComponent: string) {
    if (this._objRegisterClass.has(nameComponent)) {
      return this._objRegisterClass.get(nameComponent);
    }
    return null;
  }

  public static addEventListener(nameDisp: string, event: string, funcListener: Function, bind: Object = null, dataListener: Object = null) {
    if (this._isRun) {
      this._temporarilyList.push([nameDisp, event, funcListener, bind, dataListener]);
    } else {
      if (Utils.checkFieldObj([nameDisp, event, funcListener])) {
        let disp: Map<string, Array<EventT62>>;
        if (!this._objEventListener.has(nameDisp)) {
          disp = new Map<string, Array<EventT62>>();
          disp.set(event, []);
          disp.get(event).push(new EventT62(event, funcListener, dataListener, bind));
          this._objEventListener.set(nameDisp, disp);
        } else {
          disp = this._objEventListener.get(nameDisp);
          if (disp.has(event)) {
            let boo = true;
            disp.get(event).map(val => {
              if (val.bind !== null && bind !== null) {
                if (val.bind === bind) {
                  if (val.listener === funcListener) {
                    boo = false;
                  }
                }
              } else {
                if (val.listener === funcListener) {
                  boo = false;
                }
              }
            });
            if (boo) {
              disp.get(event).push(new EventT62(event, funcListener, dataListener, bind));
            }
          } else {
            disp.set(event, []);
            disp.get(event).push(new EventT62(event, funcListener, dataListener, bind));
          }
        }
      } else {
        console.error('DispatchEventT62 "not add to listener"  event ->' + event + '    nameDispatch ->' + nameDisp + '    funcListener ->' + funcListener);
      }
    }
  }

  public static removeEventListener(nameDisp: string, event: string, funcListener: Object) {
    if (this._isRun) {
      this._temporarilyListRemove.push([nameDisp, event, funcListener]);
    } else {
      const obj = this._objEventListener.get(nameDisp);
      if (obj) {
        const evMap = obj.get(event);
        for (let m = 0; m < evMap.length; m++) {
          if (evMap[m].listener === funcListener) {
            evMap.splice(m, 1);
          }
        }
      }
    }
  }

  public static dispatchEvent(dispObj: Object, event: string, dataDisp: Object = null): void {
    if (!this._isRun) {
      this._isRun = true;
      for (const key of this._objRegisterClass.keys()) {
        if (this._objRegisterClass.get(key) === dispObj) {
          const listener = this._objEventListener.get(key);
          if (listener) {
            for (const ev of listener.keys()) {
              if (ev === event) {
                const arrList: EventT62[] = listener.get(ev);
                arrList.map(val => {
                  val.dispatcher = dispObj;
                  val.dataDisp = dataDisp;
                  if (val.bind !== null) {
                    val.listener.bind(val.bind)(val);
                  } else {
                    val.listener(val);
                  }
                });
              }
            }
          }
        }
      }
      this._isRun = false;
      this.checkTemporarily();
    } else {
      this._temporarilyListDisp.push([dispObj, event, dataDisp]);
    }
  }

  private static checkTemporarily() {
    if (this._temporarilyListRemove.length !== 0) {
      for (let m = 0; m < this._temporarilyListRemove.length; m++) {
        const obj = this._temporarilyListRemove.shift();
        this.removeEventListener(obj[0], obj[1], obj[2]);
      }
    }
    if (this._temporarilyList.length !== 0) {
      for (let m = 0; m < this._temporarilyList.length; m++) {
        const obj = this._temporarilyList.shift();
        this.addEventListener(obj[0], obj[1], obj[2], obj[3], obj[4]);
      }
    }
    if (this._temporarilyListDisp.length !== 0) {
      for (let m = 0; m < this._temporarilyListDisp.length; m++) {
        const obj = this._temporarilyListDisp.shift();
        this.dispatchEvent(obj[0], obj[1], obj[2]);
      }
    }
  }
}

export class EventT62 {
  public event = '';
  public listener: Function = null;
  public bind: object = null;
  public dataListener: any = null;
  public dataDisp: any = null;

  public dispatcher: any = null;

  constructor(e: any, l: any, d: any, b: any) {
    this.event = e;
    this.dataListener = d;
    this.listener = l;
    this.bind = b;
  }
}
