import {IService} from './IService';
import {EventSocket, ServiceSocket} from './service-socket';
import {HttpClient} from '@angular/common/http';
import {ServiceHTTP} from './service-http';
import {DispatchEventT62, EventT62} from '../DispatchEventT62';
import {EnumEngine} from '../EnumEngine';
import {ServiceEndPoint} from './service-end-point';
import {UserService} from '../../pages/service/UserService';

export class ServiceController {

  private static inst: ServiceController = null;
  private _serviceUser: UserService;
  private _serviceHttp: IService;
  private _serviceSocket: ServiceSocket;


  public static getInst(): ServiceController {
    if (!this.inst) this.inst = new ServiceController();
    return this.inst;
  }

  private constructor() {
    DispatchEventT62.registrationObj(EnumEngine.ServiceController, this);
  }

  public initService(http: HttpClient = null, isServHTTP: boolean = false, isServUser: boolean = false, isServSocket: boolean = false): void {
    if (isServHTTP) this._serviceHttp = new ServiceHTTP(http);
    if (isServUser) this._serviceUser = new UserService();
    if (isServSocket) this._serviceSocket = new ServiceSocket();
  }


  public get serviceUser(): UserService {
    return this._serviceUser;
  }

  public get serviceHttp(): IService {
    return this._serviceHttp;
  }

  public get serviceSocket(): ServiceSocket {
    return this._serviceSocket;
  }



}
