import {Component, ComponentFactory, ComponentFactoryResolver, ComponentRef, ViewChild, ViewContainerRef} from '@angular/core';
import {TerminalComponent} from '../terminal/terminal.component';

declare const jQuery;

@Component({
  selector: 't62-page',
  templateUrl: './page.html',
  styles: []
})
export class PageComponent {

  @ViewChild('container', {read: ViewContainerRef}) container: any;

  constructor(public resolver: ComponentFactoryResolver = null) {

  };

  showDialog() {
    this.createNewTerminal();
  }

  private createNewTerminal() {
    const factory: ComponentFactory<TerminalComponent> = this.resolver.resolveComponentFactory(TerminalComponent);
    const componentRef: ComponentRef<any> = this.container.createComponent(factory);

  }

}



